@import 'vars';
@import 'mixins';

.#{$prefix}payment {
  &__title {
    font-size: 26px;
    font-weight: 300;
    letter-spacing: -0.2px;
    color: $dark-grey;
    margin-bottom: 26px;

    @include mobile {
      margin-top: 31px;
      line-height: 0.69;
      margin-bottom: 15px; } }

  &__title-2 {
    font-weight: 300;
    font-size: 25px;
    line-height: 0.72;
    letter-spacing: -0.3px;
    color: $dark-grey;
    margin-bottom: 20px; }

  &__subtitle {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.12;
    letter-spacing: -0.1px;
    color: #5d5d5d;
    display: flex;
    align-items: center;
    margin-bottom: 34px;

    @include mobile {
      font-size: 13px;
      line-height: 1.62;
      flex-direction: column;
      margin-bottom: 20px;

      .pts-svg-rapid-cogs {
        width: 24px;
        height: 20px; } }

    &-feature {
      position: relative;
      top: -8px;

      @include mobile {
        top: -5px; } } }


  &__feature {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 2.86;
    letter-spacing: -0.1px;

    @include mobile {
      line-height: normal;
      text-align: left; } }


  &__price {
    font-size: 17px;
    color: $dark-grey;
    letter-spacing: -0.3px;
    line-height: 1.06;

    &-value {
      font-size: 38px;
      line-height: 0.47; } }

  &__slider {
    margin-bottom: 22px;

    .#{$prefix}slider__value {
      font-size: 14px;
      color: $dark-grey;
      font-weight: 500; }

    &-info {
      font-size: 12px;
      color: $mid-grey; } } }
